import { useContext } from "react";
import style from "./Sidebar.module.css";
import { NavLink } from "react-router-dom";
import { userContext } from "../../context/userContext";
import { showAddModal } from "./../../utils/Note";
import { NotesContext } from "../../context/NoteContext";

export default function Sidebar({ isMinimized, setIsMiniMized }) {
  const { logOut, token } = useContext(userContext);
  const { setNotes } = useContext(NotesContext);

  return (
    <>
      <nav className={`${style.nav} shadow-sm`}>
        <button
          onClick={() => showAddModal({ updater: setNotes, token })}
          className="btn btn-main text-capitalize w-100 mb-3"
        >
          <i className="fa-solid fa-plus me-2"></i>
          {isMinimized ? "" : "New Note"}
        </button>
        <ul className="list-unstyled">
          <li>
            <NavLink to="/">
              <i className="bi bi-house-heart me-2"></i>
              {isMinimized ? "" : "Home"}
            </NavLink>
          </li>
          <li>
            <NavLink to="/search">
              <i className="bi bi-search me-2"></i>{" "}
              {isMinimized ? "" : "Search"}
            </NavLink>
          </li>
          <li>
            <span onClick={() => logOut()} className="pointer">
              <i className="bi bi-box-arrow-left me-2"></i>
              {isMinimized ? "" : "Log Out"}
            </span>
          </li>
          <li></li>
        </ul>
        <div
          onClick={() => setIsMiniMized(!isMinimized)}
          className={`${style.change} shadow pointer`}
        >
          {isMinimized ? (
            <i className={`fa-solid fa-chevron-right `}></i>
          ) : (
            <i className={`fa-solid fa-chevron-left `}></i>
          )}
        </div>
      </nav>
    </>
  );
}
